import { delArticle } from "@/api/article";
export default {
  name: "Article-meta",
  props: {
    article: {
      type: Object,
      required: true
    },
    user: {
      type: Object
    }
  },
  methods: {
    async delArticle() {
      const {
        params: { slug }
      } = this.$route;
      await delArticle(slug);
      this.$router.go(-1);
    }
  }
};
